@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff"); }

@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff"); }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0); }

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }

.main-nav__item {
  margin: 0;
  padding: 0; }

.main-nav__link {
  color: #ffffff;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  padding: 21px 20px 9px;
  display: inline-block;
  opacity: 0.7; }

.page {
  min-width: 320px; }

.page__body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", "Arial", sans-serif; }

.page a {
  text-decoration: none; }

.page__header_main {
  background-image: url("/img/promo.jpg");
  background-color: #000000;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center; }

.promo {
  padding-top: 256px;
  padding-bottom: 240px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap; }

.promo__title {
  color: #ffffff;
  margin-top: 0;
  margin-bottom: 24px; }

.promo__title_main {
  font-size: 46px;
  line-height: 64px; }

.promo__wrapper {
  width: 395px; }

.promo__text {
  font-size: 20px;
  line-height: 32px;
  color: #ffffff;
  margin: 0; }

.promo__logo {
  margin-bottom: 30px; }

.form {
  width: 327px; }

.form__heading {
  color: #ffffff;
  font-weight: bold;
  font-size: 17px;
  line-height: 28px;
  margin-top: 0;
  margin-bottom: 20px;
  text-align: center; }

.form__list {
  margin: 0;
  padding: 0;
  list-style: none; }

.form__input {
  width: 100%;
  margin-bottom: 20px;
  padding: 15px 30px;
  font-family: inherit;
  font-size: 15px;
  border-radius: 4px;
  border: none; }

.form__submit-btn {
  width: 100%;
  padding: 15px;
  font-size: 15px;
  font-weight: 600;
  color: #000000;
  background-color: #0ad9c6;
  border: none;
  border-radius: 6px; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

.container {
  width: 873px;
  margin: 0 auto; }
